import Vue from 'vue';
import store from '../../store';
/**
 * modal,el-dialogcloseemit '[data,]close'
 * componentel-dialogrefmodal
 * @param {*} component importComponent
 * @param {*} options mergeComponent
 */
export const openModal = (component) => {
    component.store = store;
    const Component = Vue.extend(component);
    
    return (options = {}) => {
        return new Promise((resolve, reject) => {
            let data;
            const modal = new Component({
                el: document.createElement('div'),
                ...options
            });

            document.body.appendChild(modal.$el);

            modal.$refs.modal.$on('close', () => {
                document.body.removeChild(modal.$el);
                resolve(data);
            });

            // FIXME: dataclose
            modal.$on('data', (d) => data = d);
        });
    };
};