<template>
	<el-dialog title="" ref="modal" :visible.sync="modalVisible" :close-on-click-modal="false" class="add-device-form">
		<el-form :model="addForm" label-width="7em" :rules="formRules" ref="addForm">
			<el-form-item label="" prop="groupName">				
					<el-input v-model="addForm.groupName" size="small" placeholder="" auto-complete="off"></el-input>				
			</el-form-item>			
			
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="modalVisible = false"></el-button>
			<el-button size="small" type="primary" @click="editUserGroupSubmit" :loading="submitLoading"></el-button>
		</div>
	</el-dialog>
</template>

<script>
	import * as _ from 'lodash';
	import { mapGetters, mapActions } from 'vuex';
	import { AdminAPI } from '../../api';

    export default {
		props: {
			initData: {
				default: null
			},
			// formType: (add|copy)|edit
			formType: {
				default: 'add',
			},
		},
        data() {
			return  {// add
				modalVisible: true,
				submitLoading: false,
				formRules: {
					groupName: [
						{ required: true, message: '', trigger: 'blur' }
						//{message: ',',pattern: /^[A-Za-z]([A-Za-z0-9]+$)/,trigger:'blur'}
					],					
				},
				addForm: {
					groupName:'',					
							
				},
			};          
        },
		computed: {		
			
		},		
		methods: {			
			editUserGroupSubmit() {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.submitLoading = true;
						const params = {
									groupName:this.addForm.groupName, 
									groupId:this.group.groupId
							};
					
						AdminAPI.editGroup(params).then(({data}) => {
							if (data.status === 0) {
								this.$message({
									message: `!`,
									type: 'success'
								});

								this.$emit('data', { result: 'success' });
								this.modalVisible = false;
							} else {
								this.$message.error(`:${data.message}`);
							}
							this.submitLoading = false;
						}).catch(() => {
							this.$message.error(`!`);
							this.submitLoading = false;
						});
					}
				});
			}
		},
		mounted() {
			this.addForm.groupName = this.group.groupName;		
		}
	}
</script>

